.red {
    color: red;
}
  
.bold {
    font-weight: bold;
}
  
.Cockpit button {
    background-color: green;
    color: white;
    padding: 14px;
    border: 1px solid blue;
    font: inherit;
    cursor: pointer;
}
.Cockpit button:hover{
    background-color: lightgreen;
    color: black;
}
.Cockpit button.red{
    background-color: red;
}
.Cockpit button.red:hover{
    background-color: salmon;
    color: black;
}